.tabs-ver {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 10px;
	
	&__item {
		display: flex;
		align-items: center;
		position: relative;
		font-size: 16px;
		font-weight: 500;
		text-decoration: none;
		color: var(--text-red);
		line-height: 1.5;		
		width: calc(100% + 40px);
		min-height: 65px;
		left: -20px;
		transition: all .2s;
		padding: 12px 20px;

		&:hover {
			color: var(--primary-red);
		}

		&.active {
			color: white;
			background-color: var(--primary-red);
		}
	}
}