.file-input {
	padding: 0;
	margin: 0;

	input {
		visibility: hidden;
		opacity: 0;
		z-index: -1;
		position: absolute;
		width: 1px;
		height: 1px;		
	}

	span {
		font-size: 14px;
		font-weight: 500;
		line-height: 1;
		color: var(--text-red);
		cursor: pointer;
		transition: all .2s;

		&:hover {
			color: var(--primary-red);
		}
	}
}