.file {
	display: flex;
	align-items: center;
	gap: 18px;
	position: relative;
	width: fit-content;

	@media (max-width: 768px) {
		align-items: flex-start;
	}

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		flex: none;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: var(--primary-red);
	}

	&__title {
		display: flex;
		flex-direction: column;
		gap: 8px;
	}

	&__name {
		font-size: 20px;
		font-weight: 500;
		color: rgba($color: #000000, $alpha: .7);
		text-decoration: none;
		transition: all .2s;

		@media (max-width: 768px) {
			font-size: 16px;
		}

		&::after {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 10;
		}
	}

	&:hover &__name {
		color: var(--primary-red);
	}

	&__parameters {
		font-size: 16px;
		font-weight: 500;
		color: #AEAEAE;

		@media (max-width: 768px) {
			font-size: 14px;
		}
	}
}