.btn {
	display: flex;
	align-items: center;
	height: 60px;
	text-decoration: none;	
	font-size: 20px;
	font-weight: 500;
	white-space: nowrap;

	@media (max-width: 1280px) {
		font-size: 18px;
	}

	&_outlined {
		justify-content: space-between;
		border: var(--primary-red) 1px solid;		
		color: #000;		
		min-width: 260px;
		padding: 0 20px 0 30px;

		img {
			margin-left: 20px;
		}
	}

	&_primary {
		justify-content: center;
		background-color: var(--primary-red);
		color: white;
		border: none;
		min-width: 212px;
		cursor: pointer;
		transition: all .2s;
		padding: 0 30px;

		&:hover {
			opacity: .85;
		}
	}
}