.docs {
	display: grid;
	grid-template-columns: 420px auto;
	gap: 160px;

	@media (max-width: 1440px) {
		grid-template-columns: 29.17vw auto;
		gap: 11.11vw;
	}

	@media (max-width: 768px) {
		grid-template-columns: 1fr;
		gap: 0;
	}

	&__title {
		line-height: 1.33;
		margin: 0 0 40px;
	}	

	&__files-list {
		display: flex;
		flex-direction: column;
		gap: 30px;
	}

	&__tabs {
		@media (max-width: 768px) {
			display: none;
		}
	}

	&__select {
		margin-bottom: 40px;
		
		@media (min-width: 769px) {
			display: none;
		}
	}
}