.filter-item {
	
	input {
		visibility: hidden;
		opacity: 0;
		z-index: -1;
		width: 1px;
		height: 1px;
		position: absolute;
	}

	span {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 40px;
		width: fit-content;
		min-width: 86px;
		border-radius: 20px;
		background-color: #F1F6FD;
		font-size: 14px;
		font-weight: 500;
		user-select: none;
		cursor: pointer;
		transition: all .1s;
		padding: 0 18px;

		@media (max-width: 768px) {
			font-size: 12px;
		}
	}

	input:checked + span {
		background-color: var(--primary-red);
		color: white;
	}	
}