.mobile-menu-popup {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: var(--primary-red);	
	visibility: hidden;
	z-index: -1;
	opacity: 0;
	transition: all .4s;
	padding: 40px 0 40px;

	&.active {
		visibility: visible;
		z-index: 9999;
		opacity: 1;
	}

	&__header {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		padding: 0 30px;		
	}

	&__close {
		width: 18px;
		height: 18px;
		background: url('../../images/menu-close-icon.svg') no-repeat center;
		background-size: contain;
		border: none;
		cursor: pointer;
	}
}