.uploaded-file {
	display: flex;
	align-items: center;
	font-size: 14px;
	font-weight: 500;
	color: #4C5869;

	&__icon {
		margin-right: 6px;
	}

	&__remove {
		width: 10px;
		height: 10px;
		background: url('../../images/delete-file-icon.svg') no-repeat center;
		background-size: contain;
		border: none;
		cursor: pointer;
		margin-left: 10px;
	}
}