.pifs-grid {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-auto-rows: minmax(100px, auto);
	gap: 20px;

	@media (max-width: 768px) {
		grid-template-columns: 1fr;
	}
	
	&__item {
		display: flex;
		align-items: center;
		background-color: #F4F7FB;
		font-size: 20px;
		color: black;
		text-decoration: none;
		line-height: 1.4;
		transition: all .2s;
		padding: 20px 40px;

		@media (max-width: 768px) {
			font-size: 16px;
			padding: 20px;
		}

		&:hover {
			color: var(--primary-red);
		}
	}
}