.license-mobile {
	display: none;
	border-bottom: 1px solid rgba($color: #000000, $alpha: .2);
	padding: 30px 0 30px;
	
	@media (max-width: 768px) {
		display: block;

		&__content {
			display: flex;
			align-items: flex-start;		
			gap: 20px;
			font-size: 12px;
			line-height: 1.5;
			color: rgba($color: #000000, $alpha: .7);

			img {
				flex: none;
			}
		}
	}
}