.contacts {	
	position: relative;
	background-color: var(--text-red);

	&__content {		
		width: 50%;
		padding: 80px 40px 80px 0;

		@media (max-width: 768px) {
			width: 100%;
			padding: 53px 0 66px;
		}
	}

	&__title {
		margin: 0 0 60px;

		@media (max-width: 768px) {
			margin: 0 0 45px;
		}
	}

	&__map {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: 50vw;
		height: 100%;

		@media (max-width: 768px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		font-size: 20px;
		font-weight: 500;
		line-height: 1.5;
		color: white;
		
		@media (max-width: 768px) {
			font-size: 16px;
		}

		&:not(:last-of-type) {
			margin: 0 0 35px;
		}

		&:last-of-type {
			margin: 0 0 54px;
		}

		a {
			color: white;
			text-decoration: none;
			transition: all .2s;

			&:hover {
				opacity: .7;
			}
		}
	}

	&__license-text {
		color: white;
		opacity: .7;
		font-size: 16px;
		font-weight: 500;
		line-height: 1.375;
		max-width: 480px;

		@media (max-width: 1440px) {
			font-size: 18px;
		}
		
		@media (max-width: 768px) {
			font-size: 14px;
		}
	}
}