.main-menu {
	@media (max-width: 768px) {
		display: none;
	}

	ul {
		display: flex;
		align-items: center;
		gap: 86px;
		padding: 0;
		margin: 0;

		@media (max-width: 1360px) {
			gap: 6.32vw;
		}

		li {
			list-style: none;			
			font-size: 20px;
			font-weight: 500;

			@media (max-width: 1440px) {
				font-size: 18px;
			}

			a {
				color: black;
				text-decoration: none;				
				transition: all .2s;

				&:hover {
					color: var(--primary-red);
				}
			}
		}
	}
}