.hero-section {
	background-color: var(--bg-gray);
	background-image: image-set(
		url('../../images/hero-image@2x.jpg') 2x,
		url('../../images/hero-image.jpg') 1x
	);
	background-repeat: no-repeat;
	background-position: right bottom;
	background-size: contain;
	padding: 80px 0 72px;

	&_inner {
		background-position: right top;
		background-size: auto;
		padding-bottom: 0;

		@media (max-width: 768px) {
			background-position: right bottom;
		}

		.header {
			margin-bottom: 190px;
		}
	}

	@media (max-width: 1440px) {
		padding-top: 60px;
	}

	@media (max-width: 768px) {
		background-image: url('../../images/hero-image_mobile.png');
		background-size: 178px;
		padding: 40px 0 40px;
	}

	&__subtitle {
		max-width: 437px;
		margin: 0 0 98px;

		@media (max-width: 1440px) {
			margin: 0 0 60px;
		}
	}

	&__license {
		display: flex;
		align-items: center;
		border-top: 1px solid rgba($color: #000000, $alpha: .2);
		font-size: 16px;
		color: rgba($color: #000000, $alpha: .7);
		line-height: 1.375;
		width: 70%;
		padding-top: 48px;

		@media (max-width: 1440px) {
			font-size: 14px;
			padding-top: 30px;
		}

		@media (max-width: 768px) {
			display: none;
		}

		img {
			margin-right: 20px;
		}

		span {
			max-width: 607px;
		}
	}

	&__logo {
		width: 100px;

		@media (max-width: 768px) {
			width: 80px;
		}
	}
}