.header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 80px;
	margin-bottom: 115px;

	@media (max-width: 1440px) {
		margin-bottom: 80px;
	}

	@media (max-width: 1360px) {
		padding: 0 40px;
	}

	@media (max-width: 768px) {
		align-items: flex-start;
		padding: 0 30px;
		margin-bottom: 40px;
	}
}