.parameters {
	padding: 98px 0 106px;	

	@media (max-width: 1280px) {
		padding: 50px 0 56px;
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 16px;

		@media (max-width: 768px) {
			flex-direction: column;
			align-items: flex-start;
			gap: 36px;
		}
	}
	
	&__item {
		display: flex;
		align-items: center;
		gap: 24px;

		@media (max-width: 1280px) {
			flex-direction: column;
			align-items: flex-start;
			gap: 16px;
		}
	}

	&__item-title-block {
		display: flex;
		align-items: center;
		gap: 16px;

		@media (max-width: 768px) {			
			gap: 12px;
		}
	}

	&__item-title {
		position: relative;
		font-size: 56px;
		font-weight: 600;
		line-height: 20px;
		color: var(--primary-red);	

		@media (max-width: 768px) {
			font-size: 48px;
		}
	}

	&__item-title-caption {
		font-size: 20px;				
		font-weight: 800;
		color: var(--primary-red);		
	}

	&__item-caption {
		font-size: 16px;
		line-height: 1.33;
		color: var(--primary-red);	
	}

	&__item-icon {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: var(--primary-red);

		@media (max-width: 768px) {
			width: 37px;
			height: 37px;

			img {
				width: 22px;
			}
		}
	}
}