.footer {
	display: flex;
	flex-direction: column;
	padding: 60px 80px 90px 80px;	

	@media (max-width: 1360px) {
		padding: 40px;
	}

	@media (max-width: 768px) {
		flex-direction: column-reverse;
		padding: 40px 30px;		
	}

	&_inner {
		background-color: #FBFBFC;
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 8vw;
		margin-bottom: 64px;

		@media (max-width: 768px) {
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 0;
		}
	}

	&__bottom {
		display: flex;
		align-items: flex-start;
		gap: 8vw;

		@media (max-width: 768px) {
			flex-direction: column-reverse;
			align-items: flex-start;
		}
	}

	&__license {
		display: flex;
		align-items: center;
		font-size: 16px;
		line-height: 1.375;
		max-width: 607px;

		@media (max-width: 1024px) {
			width: 50%;
		}

		@media (max-width: 768px) {
			align-items: flex-start;
			width: 100%;
		}

		img {
			margin-right: 20px;
		}
	}
	
	&__copyrights {
		font-size: 16px;
		line-height: 1.375;	
		color: #80878F;
		width: 23.44vw;

		@media (max-width: 1440px) {
			font-size: 14px;
		}

		@media (max-width: 1024px) {
			width: 50%;
		}

		@media (max-width: 768px) {
			font-size: 14px;
			width: 100%;			
		}
	}

	&__disclaimer {
		display: flex;
		flex-direction: column;		
		font-size: 16px;
		line-height: 1.375;
		color: #80878F;
		margin: 0;

		@media (max-width: 1024px) {
			width: 50%;
		}

		@media (max-width: 768px) {
			width: 100%;
			margin-bottom: 35px;
		}

		a {
			color: #80878F;
			text-decoration: none;
			transition: all .2s;

			&:hover {
				color: var(--text-red);
			}
		}

		span:not(:last-of-type) {
			margin-bottom: 20px;
		}
	}

	&__links {
		display: flex;
		flex-direction: column;
		flex: none;
		width: 23.44vw;

		@media (max-width: 1024px) {
			width: 50%;
		}

		@media (max-width: 768px) {
			width: 100%;
		}

		.btn {
			margin-bottom: 35px;
		}

		.red-link {
			font-size: 14px;
			margin-bottom: 13px;
		}
	}
}