.open-filters {
	display: flex;
	align-items: center;
	height: 50px;
	background: none;
	border: var(--primary-red) 1px solid;
	border-radius: 25px;
	font-size: 16px;
	font-weight: 500;
	cursor: pointer;
	white-space: nowrap;
	transition: all .2s;
	padding: 0 20px;

	&:hover {
		background-color: var(--primary-red);
		color: white;

		svg path {
			stroke: white;
		}
	}

	svg {
		margin-right: 12px;
	}
}