.tabs-hor {
	display: flex;
	align-items: flex-end;
	gap: 80px;

	@media (max-width: 1440px) {
		gap: 4.17vw;
	}

	@media (max-width: 768px) {
		flex-direction: column;
		align-items: flex-start;
		gap: 0;
	}

	&__item {		
		position: relative;
		font-size: 20px;
		font-weight: 500;
		color: var(--text-red);
		text-decoration: none;
		width: fit-content;
		transition: all .2s;
		padding-bottom: 30px;

		@media (max-width: 1440px) {
			font-size: 18px;
		}

		@media (max-width: 1024px) {
			font-size: 16px;
		}

		@media (max-width: 768px) {
			display: flex;
			align-items: center;
			font-size: 16px;
			font-weight: 500;			
			line-height: 1.375;
			height: 65px;
			width: calc(100% + 28px);
			left: -14px;
			padding: 0 14px;
		}

		svg {
			position: relative;
			bottom: -8px;
			margin-right: 12px;

			@media (max-width: 768px) {
				display: none;
			}
			
			path {
				transition: all .2s;
			}
		}
		

		&:hover {
			color: var(--primary-red);

			svg path {
				stroke: var(--primary-red);
			}
		}

		&.active {
			color: black;
			font-weight: 600;
			cursor: auto;

			@media (max-width: 768px) {
				color: white;
				background-color: var(--primary-red);
			}

			&::after {
				content: '';
				display: block;
				width: 100%;
				height: 3px;
				background-color: var(--primary-red);
				position: absolute;
				left: 0;
				bottom: 0;

				@media (max-width: 768px) {
					display: none;
				}
			}
		}		
	}
}