.info-messages {
	display: flex;
	flex-direction: column;
	gap: 42px;
	width: 77%;

	@media (max-width: 768px) {
		width: 100%;
	}

	&__item {
		display: flex;
		flex-direction: column;
		gap: 12px;
		position: relative;
	}

	&__item-title {
		font-weight: 500;
		font-size: 20px;
		line-height: 1.6;
		color: rgba($color: #000000, $alpha: .7);
		text-decoration: none;
		transition: all .2s;

		@media (max-width: 768px) {
			font-size: 16px;
		}

		&::before {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 10;
		}

		&:hover {
			color: var(--primary-red);
		}
	}

	&__item-date {
		font-size: 16px;
		font-weight: 500;
		color: var(--text-red);		
		transition: all .2s;
	}

	&:hover &__item-date {
		color: var(--primary-red);
	}
}