.contacts-content {
	display: flex;
	gap: 165px;
	width: 90%;
	padding-bottom: 92px;

	@media (max-width: 1440px) {
		gap: 11.46vw;
	}

	@media (max-width: 768px) {
		flex-direction: column;
		gap: 40px;
		width: 100%;
	}

	&__content {
		width: 40%;

		@media (max-width: 768px) {
			width: 100%;
		}
	}
	
	&__license {
		width: 60%;
		font-size: 16px;
		line-height: 1.375;
		color: rgba($color: #000000, $alpha: .5);
		margin: 0;

		@media (max-width: 768px) {
			width: 100%;
		}
	}

	.contacts__text {
		color: rgba($color: #000000, $alpha: .7);		

		a {
			color: var(--text-red);
		}
	}
}