.primary-title {
	font-weight: 600;
	font-size: 64px;
	line-height: 1;
	max-width: 810px;
	margin: 0 0 40px;

	@media (max-width: 1440px) {
		font-size: 56px;
	}

	@media (max-width: 1024px) {
		font-size: 48px;
		max-width: 700px;
	}

	@media (max-width: 768px) {
		font-size: 22px;
		line-height: 1.36;
		max-width: 80vw;
		margin: 0 0 30px;
	}
}