.select {
	position: relative;	
	
	&__input {		
		min-height: 65px;
		width: 100%;
		background: none;
		font-size: 14px;
		font-weight: 500;
		line-height: 1.33;
		text-align: left;
		border: var(--primary-red) 1px solid;
		position: relative;
		padding: 20px 60px 20px 20px;
		cursor: pointer;

		&::after {
			content: '';
			display: block;
			width: 20px;
			height: 12px;
			background: url('../../images/dropdown-arrow.svg') no-repeat center;
			background-size: contain;
			position: absolute;
			right: 20px;
			top: 25px;
			transition: all .2s;
		}
	}

	&.active &__input {
		&::after {
			transform: rotate(180deg);
		}
	}

	&__dropdown {		
		display: none;
		flex-direction: column;		
		font-size: 14px;
		font-weight: 500;
		position: relative;
		top: -1px;
		border: var(--primary-red) 1px solid;
		padding: 0;		
		margin: 0;

		li {
			list-style: none;
			cursor: pointer;
			padding: 20px;

			&.active {
				background-color: var(--primary-red);
				color: white;
			}
		}
	}

	&.active &__dropdown {
		display: flex;
	}
}