.form {
	
	&__input-block {
		position: relative;
		margin-bottom: 30px;

		&.required::before {
			content: '*';
			font-size: 20px;
			font-weight: 500;
			color: var(--primary-red);
			position: absolute;
			top: 23px;
			left: -20px;
		}
	}

	&__double-fields {
		display: flex;
		justify-content: space-between;
		gap: 3.125vw;

		@media (max-width: 768px) {
			flex-direction: column;
			justify-content: flex-start;
			gap: 0;
		}
	}

	&__submit-block {
		display: flex;
		align-items: center;
		gap: 34px;
		margin-top: 60px;

		@media (max-width: 1440px) {
			gap: 2.36vw;
		}

		@media (max-width: 1024px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__agreement {
		font-weight: 500;
		font-size: 14px;
		line-height: 1.57;
		color: #4C5869;
	}

	&__files-list {
		display: flex;
		flex-direction: column;
		gap: 16px;
		margin-top: 16px;
	}
}